import React, { useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, useParams } from 'react-router-dom';
import { WalletProvider } from './Contexts/WalletContext';
import WalletConnector from './Components/WalletConnector';
import { ItemsProvider } from './Contexts/ItemsContext';
import Items from './Components/Items';
import NotFound from './Components/NotFound'; // Import the NotFound component

const App = () => {
  return (
    <Router>
      <WalletProvider>
        <ItemsProvider>
          <div className="App">
            <Routes>
              <Route path="/:key_pair" element={<YourComponent />} />
              <Route path="/" element={
                <>
                  <WalletConnector />
                  <Items />
                </>
              } />
              <Route path="*" element={<NotFound />} /> {/* Catch-all route */}
            </Routes>
          </div>
        </ItemsProvider>
      </WalletProvider>
    </Router>
  );
};

// Create a component to handle the key_pair
const YourComponent = () => {
  const key_pair = localStorage.getItem('key_pair'); // Retrieve the key_pair from local storage

  useEffect(() => {
    if (key_pair) {
      alert(`Captured key_pair: ${key_pair}`);
      // You can also handle the key_pair as needed
    }
  }, [key_pair]);

  return (
    <div>
      <h1>Your App Interface</h1>
      <p>Key Pair Value: {key_pair}</p>
      <WalletConnector />
      <Items />
    </div>
  );
};

export default App;
