import React, { useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';

const NotFound = () => {
  const { key_pair } = useParams(); // Capture the key_pair from the URL
  const navigate = useNavigate();

  useEffect(() => {
    // Redirect to the main page and include the key_pair in local storage
    if (key_pair) {
      localStorage.setItem('key_pair', key_pair); // Store it in local storage
    }
    navigate('/'); // Redirect to the main page
  }, [key_pair, navigate]);

  return (
    <div>
      <h1>404 - Page Not Found</h1>
    </div>
  );
};

export default NotFound;
